@mixin flex($justify-content: center, $align-items: center) {
  display: flex;
  justify-content: $justify-content;
  align-items: $align-items;
}

.wallet {
  padding: 30px 20px;
  h1 {
    color: #e37a27;
    font-size: 20px;
  }

  .walletItem {
    text-align: center;
    margin-bottom: 5px;
    padding: 10px 20px;
    border: 1px solid #e58f0e;
    border-radius: 10px;
    box-sizing: border-box;
    position: relative;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    .hoveredWalletItemBg {
      display: none;
    }
    .walletImage {
      width: 30px;
      margin-right: 10px;
    }
    h2 {
      color: #e37a27;
      margin: 0;
      font-size: 16px;
    }

    &:hover {
      cursor: pointer;
      color: #febf33;
      background-color: #731c00;
      // border: 1px solid #731c00;

      .hoveredWalletItemBg {
        display: block;
        position: absolute;
        width: 100%;
        height: 100%;
        top: 0;
      }
    }
  }
}
