@mixin flex($justify-content: center, $align-items: center) {
  display: flex;
  justify-content: $justify-content;
  align-items: $align-items;
}

.Layout {
  width: 100vw;
  height: 100vh;
  overflow-x: hidden;
  overflow-y: scroll;
  position: relative;
  background-image: url('../../assets/images/index/bg.png');
  background-color: #862100;
  // background-image: radial-gradient(
  //     56.67% 56.67% at 50% 50%,
  //     rgba(255, 87, 50, 0) 0%,
  //     rgba(120, 38, 20, 0.65) 74.46%,
  //     rgba(51, 14, 2, 0.7) 100%
  //   ),
  //   url('../../../public/static/images/index/bg.png');
  // background-image: url('../../../public/static/images/index/bg.png');

  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

.LayoutContent {
  // width: 978px;
  max-width: 900px;
  width: 85%;
  margin: 0 auto;
}

@media (max-width: 765px) {
  .LayoutContent {
    max-width: none;
    width: 90%;
  }
}
