@mixin flex($justify-content: center, $align-items: center) {
  display: flex;
  justify-content: $justify-content;
  align-items: $align-items;
}

@import './assets/styles/font/main.scss';

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.App {
  width: 100vw;
  height: 100vh;
  overflow: auto;
  color: #fff;
  font-family: 'Inter', -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans',
    'Helvetica Neue', sans-serif;
}

.customModal {
  &.ant-modal-wrap {
    // left: 200px;
  }
  .ant-modal-content {
    overflow-y: scroll;
    // max-height: 70vh;
    min-height: 300px;
    border-radius: 20px;
    // box-shadow: 0px 0px 10px rgba(171, 238, 247, 0.6), inset 0px 0px 10px #186065;
    width: 450px;
    max-width: 100%;
    margin: 0 auto;
    -ms-overflow-style: none; /* IE and Edge */
    scrollbar-width: none; /* Firefox */
    border: 2px solid #fbba08;
    // background-color: black;
    background-color: #742611;

    &::-webkit-scrollbar {
      display: none;
    }

    .ant-modal-body {
      padding: 10px 0;

      .close {
        position: absolute;
        right: 30px;
        top: 30px;
        z-index: 3;
        cursor: pointer;
        width: 25px;
        opacity: 0.9;
        font-size: 20px;
        color: white;
      }
      .title {
        width: 100%;
        text-align: center;
        position: absolute;
        top: 10px;
        color: white;
        font-size: 20px;
        font-weight: 600;
        left: 50%;
        transform: translateX(-50%);
      }
      .modalHeader {
        width: calc(100% - 20px);
        margin: 10px 10px;
      }
    }
  }
  .ant-modal-close-x {
    color: white;
    display: none;
  }
}
