@mixin flex($justify-content: center, $align-items: center) {
  display: flex;
  justify-content: $justify-content;
  align-items: $align-items;
}

.PreSale {
  position: relative;
  width: 100%;
}

.Card {
  @include flex(space-between);
  gap: 20px;
  margin-top: 43px;
  align-items: stretch;
}

.CardItem {
  flex: 1;
  // width: 480px;
  // height: 439px;
  background: #742611;
  border-radius: 20px;
  padding: 20px;
}

.CardItemHeader {
  @include flex(flex-start);
  column-gap: 20%;
  width: 100%;
  img {
    width: 30%;
  }
}

.CardTitle {
  font-weight: 700;
  font-size: 24px;
  line-height: 28px;
  color: #e37a27;
}

.ClubCopies {
  @include flex(space-between);
  font-weight: 700;
  font-size: 16px;
  line-height: 16px;
  color: #e37a27;
  margin-top: 38px;
}

.ClubCopiesTotal {
  color: #fff;
}

.ClubProgress {
  width: 420px;
  width: 100%;
  height: 18px;
  background: #491707;
  border: 1px solid #dc7526;
  border-radius: 9px;
  margin-top: 10px;
  overflow: hidden;
}

.ClubProgressValue {
  background: #e37a27;
  border-radius: 7px;
  width: 0;
  height: 100%;
}

.ClubTotal {
  @include flex(space-between, flex-start);

  font-weight: 400;
  font-size: 20px;
  color: #e37a27;
  margin-top: 39px;
}

.ClubTotalValue {
  // font-weight: 700;
  display: inline-block;
  text-align: right;
  font-size: 18px;
}

.ClubTotalBnb {
  // font-weight: 700;
  font-size: 16px;
}

.Amount {
  @include flex(space-between, flex-start);
  flex-direction: column;
  background: #491707;
  border: 1px solid #dc7526;
  border-radius: 8px;
  width: 100%;
  // height: 100px;
  margin-top: 30px;
  padding: 10px 20px;
  color: #e37a27;
  font-weight: 400;
  font-size: 20px;
  margin-bottom: 30px;
  row-gap: 10px;
}
.AmountTitleWrap {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.AmountInputWrap {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  column-gap: 10px;
  input {
    flex: 1;
    background: none;
    outline: none;
    border: 0px;
    color: #fff;
    font-size: 16px;
  }
}
.UnitWrap {
  display: flex;
  align-items: center;
  column-gap: 5px;
  color: #f3ba2f;
  img {
    width: 20px;
  }
}

.AmountMax {
  font-weight: 700;
  font-size: 20px;
  color: #e37a27;
  cursor: pointer;
}

.Field {
  @include flex(space-between, flex-start);
  // font-weight: 700;
  font-size: 18px;
  line-height: 32px;
  text-align: right;
  text-transform: uppercase;

  color: #e37a27;
}

.Button {
  margin-top: 30px;
}

.Tab {
  @include flex();
}

.TabItem {
  font-weight: 700;
  font-size: 20px;
  line-height: 20 px;
  text-align: center;
  color: #964817;
  margin: 30px;
  cursor: pointer;
}

.TabItemActive {
  color: #e37a27;
}

.TabTitle {
  font-weight: 700;
  font-size: 20px;
  line-height: 20px;
  text-align: center;
  text-transform: uppercase;
  color: #e37a27;
  margin-bottom: 40px;
}

.TabContent {
  @include flex(space-between, flex-start);

  // height: 358px;
  height: fit-content;
  background: #491707;
  border: 1px solid #892721;
  border-radius: 30px;
  overflow: hidden;
}

.TabContentItem {
  flex: 1;
  width: 50%;
  // max-width: 500px;
  padding: 40px 30px;
}

.TabField {
  @include flex(space-between);

  // font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  color: #964817;
}

.TabFieldValue {
  font-weight: 600;
  font-size: 15px;
  line-height: 28px;
  /* or 180% */

  text-align: right;
  text-transform: uppercase;

  color: #e37a27;
  overflow-x: scroll;
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */

  &::-webkit-scrollbar {
    display: none;
  }
}

.TabContentItemAdd {
  font-weight: 700;
  font-size: 16px;
  line-height: 24px;
  color: #e37a27;
  text-align: center;
  margin-top: 20px;
  cursor: pointer;
}

.TabContentItemButton {
  @include flex;

  width: 100%;
  height: 100%;
  padding-top: 30px;
}

.InviteWrap {
  width: 100%;
  position: relative;
  display: flex;
  align-items: flex-start;
  flex-direction: column;
  margin: 50px 0;
  row-gap: 15px;
  h2 {
    font-weight: 700;
    font-size: 20px;
    line-height: 20px;
    text-transform: uppercase;
    color: #e37a27;
  }
  p {
    font-weight: 400;
    font-size: 18px;
    line-height: 22px;
    color: #bb642f;
  }
}
.ReferralLink {
  width: 100%;
  border: 1px solid #e37a27;
  border-radius: 10px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px;
  img {
    width: 25px;
  }
}
.ReferralText {
  font-weight: 700;
  font-size: 20px;
  line-height: 20px;
  color: #e37a27;
  overflow: scroll;
  // text-overflow: ellipsis;
  white-space: nowrap;
  overflow-x: scroll;
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */

  &::-webkit-scrollbar {
    display: none;
  }
}
.ReferralStatsWrap {
  width: 100%;
  background: #73220b;
  border-radius: 20px;
  display: flex;
  align-items: center;
  column-gap: 20px;
  padding: 15px;
}
.ReferralStats {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: space-around;
  column-gap: 5px;
}
.ReferralStatsItem {
  h2 {
    color: white;
    font-size: 18px;
    line-height: 18px;
  }
  p {
    margin: 0;
    margin-top: 5px;
  }
}
.CopyIcon {
  cursor: pointer;
}
.ClaimButton {
  width: auto;
  height: auto;
  padding: 5px;
}
@media (max-width: 765px) {
  .Card {
    align-items: center;
    flex-direction: column;
  }
  .CardTitle {
    font-size: 22px;
    line-height: 26px;
    color: #e37a27;
  }
  .CardItem {
    width: 100%;
  }

  .TabContent {
    flex-direction: column;
    align-items: center;
  }
  .TabContentItemButton {
    padding: 0;
  }
  .Button {
    display: flex;
    justify-content: center;
  }
  .ReferralStatsWrap {
    flex-direction: column;
    align-items: center;
    row-gap: 10px;
  }
  .ReferralStats {
    width: 100%;
  }
  .ReferralStatsItem {
    h2 {
      font-size: 16px;
      line-height: 16px;
    }
    p {
      font-size: 14px;
    }
  }
  .TabContentItem {
    width: 100%;
  }
}
