@mixin flex($justify-content: center, $align-items: center) {
  display: flex;
  justify-content: $justify-content;
  align-items: $align-items;
}

.IndexDesc {
  font-weight: 700;
  font-size: 18px;
  line-height: 22px;
  text-align: center;
  color: #e6c5c5;
  margin-top: 30px;

  p {
    margin-bottom: 24px;
  }
}

.IndexDescIcon {
  @include flex();
  width: 100%;
  align-items: stretch;
  gap: 3%;
  margin: 50px 0;
}

.IndexDescIconItem {
  @include flex();

  flex: 1;
  // column-gap: 10px;
  justify-content: space-around;
  align-items: center;
  background: #742611;
  border: 2px solid #ca5350;
  border-radius: 20px;
  font-weight: 700;
  font-size: 20px;
  line-height: 20px;
  color: #ffffff;
  padding: 15px;
  text-align: center;

  img {
    width: 30%;
  }
}

.Title {
  font-weight: 700;
  font-size: 36px;
  text-align: center;
  color: #ffffff;
}

.StatsIcon {
  @include flex(space-between, flex-start);
  width: 100%;
  flex-wrap: wrap;
  text-align: center;
  margin-top: 50px;
  row-gap: 30px;
}
.StatsIconItem {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.StatsIconContainer {
  @include flex;
  width: 50%;
  max-width: 120px;
  background: #742611;
  border: 2px solid #ca5350;
  border-radius: 50%;
  img {
    width: 50%;
  }
}

.StatsIconItemText {
  font-weight: 700;
  font-size: 18x;
  line-height: 22px;
  /* identical to box height, or 120% */

  text-align: center;
  text-transform: uppercase;

  color: #ed6156;
}

.Activity {
  width: 100%;
  background: #742611;
  border: 2px solid #ca5350;
  border-radius: 30px;
  padding: 20px 40px 60px;
  margin: 50px 0;
}

.ActivityTitle {
  font-weight: 700;
  font-size: 20px;
  line-height: 20px;
  /* identical to box height, or 100% */

  text-align: center;
  text-transform: uppercase;

  color: #ffffff;
}

.ActivityList {
  @include flex(space-between);
  font-weight: 700;
  font-size: 18px;
  line-height: 22px;
  /* identical to box height, or 120% */

  text-align: center;
  text-transform: uppercase;

  color: #ed6156;
  margin-top: 20px;
}

@media (max-width: 765px) {
  .IndexDesc {
    font-size: 16px;
    line-height: 20px;
  }
  .IndexDescIconItem {
    font-size: 16px;
    line-height: 16px;
    padding: 10;
    // column-gap: 5px;
    // img {
    //   width: 40px;
    // }
  }
  .Title {
    font-size: 28px;
  }
  .StatsIconItem {
    width: 50%;
    flex: auto;
  }
}
