@mixin flex($justify-content: center, $align-items: center) {
  display: flex;
  justify-content: $justify-content;
  align-items: $align-items;
}

.WalletButton {
  position: relative;
  cursor: pointer;
  text-decoration: none;
  img {
    width: 90px;
  }
  p {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -60%);
    font-weight: 700;
    font-size: 16px;
    line-height: 16px;
    color: #e6c5c5;
    margin: 0;
  }
  &.active,
  &:hover {
    p {
      color: #fecf76;
    }
  }
}
