@mixin flex($justify-content: center, $align-items: center) {
  display: flex;
  justify-content: $justify-content;
  align-items: $align-items;
}

.CountdownBanner {
  position: relative;
  width: 100%;

  background: #742611;
  border: 2px solid #ca5350;
  border-radius: 30px;
  overflow: hidden;

  background-image: url('../../assets/images/countdownBanner/banner.png');
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;

  display: flex;
  flex-direction: column;
  align-items: center;
  row-gap: 20px;
  padding: 20px;

  .logo {
    width: 20%;
    min-width: 120px;
  }
  .countdownWrap {
    background: #e37a27;
    border-radius: 24px;
    // width: 100%;
    padding: 10px 20px;
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 15px;
    color: #491707;
  }
  .participateButton {
    text-decoration: none;
  }
}
