@mixin flex($justify-content: center, $align-items: center) {
  display: flex;
  justify-content: $justify-content;
  align-items: $align-items;
}

.Footer {
  @include flex(space-between);

  padding: 57px 0;
}

.FooterIcon {
  @include flex;
  gap: 10px;
  .IconItem {
    margin: 0;
    img {
      width: 25px;
    }
  }
}

.FooterText {
  font-weight: 700;
  font-size: 20px;
  line-height: 24px;
  /* identical to box height, or 120% */

  text-align: center;
  text-transform: uppercase;

  color: #ed6156;
}

.IconItem {
  margin-left: 25px;
}
