@mixin flex($justify-content: center, $align-items: center) {
  display: flex;
  justify-content: $justify-content;
  align-items: $align-items;
}

.Button {
  @include flex;

  min-width: 120px;
  height: 60px;
  padding: 20px;
  background: #e37a27;
  border-radius: 10px;
  font-weight: 700;
  font-size: 20px;
  color: #491707;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans',
    'Helvetica Neue', sans-serif;
  cursor: pointer;
  &:hover {
    opacity: 0.9;
  }

  &:active {
    opacity: 0.8;
  }
  &.loading {
    opacity: 0.8;
    pointer-events: none;
  }
}

@media (max-width: 765px) {
  .Button {
    min-width: 100px;
    height: 48px;
    font-size: 18px;
  }
}
